//
// Variables
// --------------------------------------------------


// COLORS:
// --------------------------------------------------

// Brand: Added 3-16-2016
// -------------------------
$brandcolor-chevy:		#cd9834;
$brandcolor-buick:		#c9480c;
$brandcolor-gmc:		#cc0000;
$brandcolor-cadillac:	#bead76;
$brandcolor-gm:			#134a9c;


// White
// -------------------------
$white:					#ffffff;



// Grays
// -------------------------
$gray-darkest:			#454545;
$gray-darker:			#959595;   			   
$gray-dark:				#acacac;
$gray:					#cacaca;
$gray-light:			#e5e5e5;
$gray-lighter:			#f0f0f0;


// Greens
// -------------------------

$green:                   #299508;
$green-light:             #d9f2c1;
$green-lighter:           #f0f7e9;


// Reds
// -------------------------
$red:                   #ed211b;
$red-light:             #fdb4b4;
$red-lighter:           #f8e9e9;




// Yellow
// -------------------------
$yellow:					#ecc30f;


// Blue
// -------------------------
$blue-dark:				#06214e; //Primary Blue hover
$blue:					#134a9c; //Primary Blue 
$blue-light:			#2a4f8e; //Secondary Blue hover, Link color Hover
$blue-lighter:			#4178da; //Secondary Blue, Link color
$blue-lightest:			#bdd0f2; //Secondary Blue Disabled


// Aqua
// -------------------------
$blue-aqua:				#02aabe;



// Brand colors
// -------------------------

$onstar:                #004a8a;
$brand-primary:         #e6ac39;
$brand-primary-light:   #f8ca43; // Light Yellow for Button
$brand-primary-dark:    #f0ad00; // Dark Yellow
$brand-success:         $green;
$brand-warning:         $red;
$brand-danger:          $red;
$brand-info:            $gray-dark;
$brand-yellow-warning:  $yellow;


// Scaffolding
// -------------------------

$body-bg:               $gray-lighter;
$text-color:            $gray-darkest;

// Links
// -------------------------

$link-color:            $blue-lighter;
$link-hover-color:		$blue-light;

// Typography
// -------------------------






// $font-family-sans-serif:  "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-family-chevy:		  "louis_bold_italicbold_italic", Arial, sans-serif;
$font-family-chevy2:      "louisregular", Arial, sans-serif;
$font-family-bold:   	  "louis_boldbold", Arial, sans-serif;
$font-family-sans-serif:  Arial, sans-serif;
$font-family-serif:       Georgia, "Times New Roman", Times, serif;
$font-family-monospace:   Monaco, Menlo, Consolas, "Courier New", monospace;
$font-family-base:        $font-family-sans-serif;

$font-size-base:          16px;
$font-size-large:         18px;
$font-size-xlarge:        25px;
$font-size-small:         ceil($font-size-base * 0.85); // ~12px

$line-height-base:        1.35; // 17/13
$line-height-computed:    floor($font-size-base * $line-height-base); // ~17px

$headings-font-family-chevy:    $font-family-chevy;
$headings-font-family-chevy2:   $font-family-chevy2;
$headings-font-family:    		$font-family-base;
$headings-font-weight:    		700;
$headings-line-height-small:	1;
$headings-line-height:    		1.3;

// Iconography
// -------------------------

$icon-font-path:          		  "../../fonts/";
$icon-font-name:          		  "FontAwesome";



// Components
// -------------------------
// Based on 14px font-size and 1.428 line-height (~20px to start)

$padding-base-vertical:          10px;
$padding-base-horizontal:        16px;

$padding-large-vertical:         10px;
$padding-large-horizontal:       16px;

$padding-xlarge-vertical:        20px;


$padding-small-vertical:         5px;
$padding-small-horizontal:       10px;

$line-height-large:              1.33;
$line-height-small:              1.5;

$border-radius-base:             3px;
$border-radius-small:            2px;
$border-radius-large:            6px;
$border-radius-xl:               8px;


$component-active-bg:            $brand-primary;

$caret-width-base:               4px;
$caret-width-large:              5px;

// Tables
// -------------------------

$table-cell-padding:                 8px;
$table-condensed-cell-padding:       5px;

$table-bg:                           transparent; // overall background-color
$table-bg-accent:                    #f9f9f9; // for striping
$table-bg-hover:                     #f5f5f5;
$table-bg-active:                    $table-bg-hover;

$table-border-color:                 #ddd; // table and cell border


// Buttons
// -------------------------

$btn-font-weight:                bold;

$btn-default-color:              #fff;
$btn-default-bg:                 $gray-dark;
$btn-default-border:             darken($gray-dark, 0%);

$btn-primary-color:              #fff;
$btn-primary-bg:                 $blue;
$btn-primary-border:             darken($btn-primary-bg, 0%);

$btn-secondary-color:             $blue-lighter;
$btn-secondary-bg:                transparent;
$btn-secondary-border:            $blue-lighter;

$btn-onstar-color:             #fff;
$btn-onstar-bg:                $onstar;
$btn-onstar-border:            darken($onstar, 0%);

$btn-success-color:              #fff;
$btn-success-bg:                 $brand-success;
$btn-success-border:             darken($btn-success-bg, 0%);

$btn-warning-color:              #fff;
$btn-warning-bg:                 $brand-warning;
$btn-warning-border:             darken($btn-warning-bg, 0%);

$btn-danger-color:               #fff;
$btn-danger-bg:                  $brand-danger;
$btn-danger-border:              darken($btn-danger-bg, 0%);

$btn-info-color:                 $gray-dark;
$btn-info-bg:                    $gray-light;
$btn-info-border:                $gray;

$btn-link-disabled-color:        $gray-light;


// Forms
// -------------------------

$input-bg:                       #fff;
$input-bg-disabled:              $gray-light;

$input-color:                    $gray-dark;
$input-border:                   $gray;
$input-border-radius:            $border-radius-base;
$input-border-focus:             $gray-dark;

$input-color-placeholder:        $gray;
$input-height-small:			 1px;
$input-height-base:              ($line-height-computed + ($padding-base-vertical * 2) + 2);
$input-height-large:             (floor($font-size-large * $line-height-large) + ($padding-large-vertical * 2) + 2);


$legend-color:                   $gray-dark;
$legend-border-color:            $gray-light;

$input-group-addon-bg:           $gray-light;
$input-group-addon-border-color: $input-border;



// Dropdowns
// -------------------------

$dropdown-bg:                    #fff;
$dropdown-border:                rgba(0,0,0,.15);
$dropdown-fallback-border:       $gray;
$dropdown-divider-bg:            $gray-light;

$dropdown-link-active-color:     #fff;
$dropdown-link-active-bg:        $component-active-bg;

$dropdown-link-color:            $gray-dark;
$dropdown-link-hover-color:      #fff;
$dropdown-link-hover-bg:         $dropdown-link-active-bg;

$dropdown-link-disabled-color:   $gray-light;

$dropdown-header-color:          $gray-light;

$dropdown-caret-color:           $gray-dark;


// COMPONENT VARIABLES
// --------------------------------------------------


// Z-index master list
// -------------------------
// Used for a bird's eye view of components dependent on the z-axis
// Try to avoid customizing these :)

$zindex-navbar:            1000;
$zindex-dropdown:          1000;
$zindex-popover:           1010;
$zindex-tooltip:           1030;
$zindex-navbar-fixed:      1030;
$zindex-modal-background:  1040;
$zindex-modal:             1050;

// Media queries breakpoints
// --------------------------------------------------

// // Extra small screen / phone
// $screen-xss:              300px;


// Extra small screen / phone
$screen-xs:                  480px;
$screen-phone:               $screen-xs;

// Small screen / tablet
$screen-sm:                  768px;
$screen-tablet:              $screen-sm;

// Medium screen / desktop
$screen-md:                  992px;
$screen-desktop:             $screen-md;

// Large screen / wide desktop
$screen-lg:                  1200px;
$screen-lg-desktop:          $screen-lg;

// So media queries don't overlap when required, provide a maximum
$screen-xs-max:              ($screen-sm - 1);
$screen-sm-max:              ($screen-md - 1);
$screen-md-max:              ($screen-lg - 1);


// Grid system
// --------------------------------------------------

// Number of columns in the grid system
$grid-columns:              12;
// Padding, to be divided by two and applied to the left and right of all columns
$grid-gutter-width:         30px;
// Point at which the navbar stops collapsing
$grid-float-breakpoint:     $screen-tablet;


// Navbar
// -------------------------

// Basics of a navbar
$navbar-height:                    50px;
$navbar-margin-bottom:             0px;//$line-height-computed;
$navbar-default-color:             #777;
$navbar-default-bg:                $gray-darker;
$navbar-default-border:            darken($navbar-default-bg, 6.5%);
$navbar-border-radius:             $border-radius-base;
$navbar-padding-horizontal:        floor($grid-gutter-width / 2);
$navbar-padding-vertical:          (($navbar-height - $line-height-computed) / 2);

// Navbar links
$navbar-default-link-color:                #777;
$navbar-default-link-hover-color:          #fff;
$navbar-default-link-hover-bg:             transparent;
$navbar-default-link-active-color:         #fff;
$navbar-default-link-active-bg:            darken($navbar-default-bg, 6.5%);
$navbar-default-link-disabled-color:       #ccc;
$navbar-default-link-disabled-bg:          transparent;

// Navbar brand label
$navbar-default-brand-color:               $navbar-default-link-color;
$navbar-default-brand-hover-color:         darken($navbar-default-link-color, 10%);
$navbar-default-brand-hover-bg:            transparent;

// Navbar toggle
$navbar-default-toggle-hover-bg:           #ddd;
$navbar-default-toggle-icon-bar-bg:        #ccc;
$navbar-default-toggle-border-color:       #ddd;


// Inverted navbar
//
// Reset inverted navbar basics
$navbar-inverse-color:                      $gray-light;
$navbar-inverse-bg:                         #222;
$navbar-inverse-border:                     darken($navbar-inverse-bg, 10%);

// Inverted navbar links
$navbar-inverse-link-color:                 $gray-light;
$navbar-inverse-link-hover-color:           #fff;
$navbar-inverse-link-hover-bg:              transparent;
$navbar-inverse-link-active-color:          $navbar-inverse-link-hover-color;
$navbar-inverse-link-active-bg:             darken($navbar-inverse-bg, 10%);
$navbar-inverse-link-disabled-color:        #444;
$navbar-inverse-link-disabled-bg:           transparent;

// Inverted navbar brand label
$navbar-inverse-brand-color:                $navbar-inverse-link-color;
$navbar-inverse-brand-hover-color:          #fff;
$navbar-inverse-brand-hover-bg:             transparent;

// Inverted navbar search
// Normal navbar needs no special styles or vars
$navbar-inverse-search-bg:                  lighten($navbar-inverse-bg, 25%);
$navbar-inverse-search-bg-focus:            #fff;
$navbar-inverse-search-border:              $navbar-inverse-bg;
$navbar-inverse-search-placeholder-color:   #ccc;

// Inverted navbar toggle
$navbar-inverse-toggle-hover-bg:            #333;
$navbar-inverse-toggle-icon-bar-bg:         #fff;
$navbar-inverse-toggle-border-color:        #333;


// Navs
// -------------------------

$nav-link-padding:                          10px 15px;
$nav-link-hover-bg:                         $gray-lighter;

$nav-disabled-link-color:                   $gray-light;
$nav-disabled-link-hover-color:             $gray-light;

$nav-open-link-hover-color:                 #fff;
$nav-open-caret-border-color:               #fff;

// Tabs
$nav-tabs-border-color:                     #ddd;

$nav-tabs-link-hover-border-color:          $gray-lighter;

$nav-tabs-active-link-hover-bg:             $body-bg;
$nav-tabs-active-link-hover-color:          $gray;
$nav-tabs-active-link-hover-border-color:   #ddd;

$nav-tabs-justified-link-border-color:            #ddd;
$nav-tabs-justified-active-link-border-color:     $body-bg;

// Pills
$nav-pills-active-link-hover-bg:            $component-active-bg;
$nav-pills-active-link-hover-color:         #fff;


// Pagination
// -------------------------

$pagination-bg:                        #fff;
$pagination-border:                    #ddd;

$pagination-hover-bg:                  $gray-lighter;

$pagination-active-bg:                 $brand-primary;
$pagination-active-color:              #fff;

$pagination-disabled-color:            $gray-light;


// Pager
// -------------------------

$pager-border-radius:                  15px;
$pager-disabled-color:                 $gray-light;


// Jumbotron
// -------------------------

$jumbotron-padding:              30px;
$jumbotron-color:                inherit;
$jumbotron-bg:                   $gray-lighter;
$jumbotron-heading-color:        inherit;


// Form states and alerts
// -------------------------

$state-warning-text:             $brand-warning;
$state-warning-bg:               $red-light;
$state-warning-border:           $brand-warning;

$state-danger-text:              $brand-danger;
$state-danger-bg:                $red-light;
$state-danger-border:            $brand-danger;

$state-success-text:             $brand-success;
$state-success-bg:               $green-light;
$state-success-border:           $brand-success;

$state-info-text:                $gray-dark;
$state-info-bg:                  $gray-lighter;
$state-info-border:              $gray;




// Tooltips
// -------------------------
$tooltip-max-width:           200px;
$tooltip-color:               #fff;
$tooltip-bg:                  #000;

$tooltip-arrow-width:         5px;
$tooltip-arrow-color:         $tooltip-bg;


// Popovers
// -------------------------
$popover-bg:                          #fff;
$popover-max-width:                   276px;
$popover-border-color:                 $blue-lighter;
$popover-fallback-border-color:        $blue-lighter;

$popover-title-bg:                    darken($popover-bg, 3%);

$popover-arrow-width:                 10px;
$popover-arrow-color:                 #fff;

$popover-arrow-outer-width:           ($popover-arrow-width + 1);
$popover-arrow-outer-color:           $blue-lighter;
$popover-arrow-outer-fallback-color:  $blue-lighter;


// Labels
// -------------------------

$label-default-bg:            $gray-light;
$label-primary-bg:            $brand-primary;
$label-success-bg:            $brand-success;
$label-info-bg:               $brand-info;
$label-warning-bg:            $brand-warning;
$label-danger-bg:             $brand-danger;

$label-color:                 #fff;
$label-link-hover-color:      #fff;


// Modals
// -------------------------
$modal-inner-padding:         40px;

$modal-title-padding:         40px;
$modal-title-line-height:     $line-height-base;

$modal-content-bg:                             $white;
$modal-content-border-color:                   $gray;
$modal-content-fallback-border-color:          $gray;

$modal-backdrop-bg:           #000;
$modal-header-border-color:   $gray;
$modal-footer-border-color:   $modal-header-border-color;


// Alerts
// -------------------------
$alert-padding:               15px;
$alert-border-radius:         $border-radius-base;
$alert-link-font-weight:      bold;

$alert-success-bg:            $state-success-bg;
$alert-success-text:          $state-success-text;
$alert-success-border:        $state-success-border;

$alert-info-bg:               $state-info-bg;
$alert-info-text:             $text-color;
$alert-info-border:           $state-info-border;

$alert-warning-bg:            $state-warning-bg;
$alert-warning-text:          $state-warning-text;
$alert-warning-border:        $state-warning-border;

$alert-danger-bg:             $state-danger-bg;
$alert-danger-text:           $state-danger-text;
$alert-danger-border:         $state-danger-border;


// Progress bars
// -------------------------
$progress-bg:                 #f5f5f5;
$progress-bar-color:          #fff;

$progress-bar-bg:             $brand-primary;
$progress-bar-success-bg:     $brand-success;
$progress-bar-warning-bg:     $blue-lighter;
$progress-bar-danger-bg:      $brand-danger;
$progress-bar-info-bg:        $brand-info;
$progress-bar-timeline-fill-bg:		  $blue-aqua;


// List group
// -------------------------
$list-group-bg:               #fff;
$list-group-border:           #ddd;
$list-group-border-radius:    $border-radius-base;

$list-group-hover-bg:         #f5f5f5;
$list-group-active-color:     #fff;
$list-group-active-bg:        $component-active-bg;
$list-group-active-border:    $list-group-active-bg;

$list-group-link-color:          #555;
$list-group-link-heading-color:  #333;


// cards
// -------------------------
$card-bg:                    #fff;
$card-inner-border:          #ddd;
$card-border-radius:         0px;
$card-footer-bg:             #f5f5f5;

$card-default-text:         $gray-darkest;
$card-default-border:       $gray;
$card-default-heading-bg:   $white;

$card-default-icon-text:          $white;
$card-default-icon-border:        $gray;
$card-default-icon-heading-bg:    $gray-darkest;

$card-default-expand-text:          $gray-dark;
$card-default-expand-border:        $gray;
$card-default-expand-heading-bg:    $white;
$card-expanded-bg-color:			$white;

$card-primary-text:          $white;
$card-primary-border:        $gray;
$card-primary-heading-bg:    $brand-primary;

$card-informational-text:          $white;
$card-informational-border:        $gray;
$card-informational-heading-bg:    $brand-primary;


$card-primary-icon-text:          $white;
$card-primary-icon-border:        $gray;
$card-primary-icon-heading-bg:    $brand-primary-dark;

$card-secondary-text:          $white;
$card-secondary-border:        $gray;
$card-secondary-heading-bg:    $blue;




$card-tertiary-text:          $white;
$card-tertiary-border:        $gray;
$card-tertiary-heading-bg:    $gray-light;


$card-success-text:          $state-success-text;
$card-success-border:        $state-success-border;
$card-success-heading-bg:    $state-success-bg;

$card-warning-text:          $state-warning-text;
$card-warning-border:        $state-warning-border;
$card-warning-heading-bg:    $state-warning-bg;

$card-danger-text:           $state-danger-text;
$card-danger-border:         $state-danger-border;
$card-danger-heading-bg:     $state-danger-bg;

$card-info-text:             $state-info-text;
$card-info-border:           $state-info-border;
$card-info-heading-bg:       $state-info-bg;


// Thumbnails
// -------------------------
$thumbnail-padding:           4px;
$thumbnail-bg:                $body-bg;
$thumbnail-border:            #ddd;
$thumbnail-border-radius:     $border-radius-base;

$thumbnail-caption-color:     $text-color;
$thumbnail-caption-padding:   9px;


// Wells
// -------------------------
$well-bg:                     #f5f5f5;


// Badges
// -------------------------
$badge-color:                 #fff;
$badge-link-hover-color:      #fff;
$badge-bg:                    $gray-light;

$badge-active-color:          $link-color;
$badge-active-bg:             #fff;

$badge-font-weight:           bold;
$badge-line-height:           1;
$badge-border-radius:         10px;


// Breadcrumbs
// -------------------------
$breadcrumb-bg:               #f5f5f5;
$breadcrumb-color:            #ccc;
$breadcrumb-active-color:     $gray-light;


// Carousel
// ------------------------

$carousel-text-shadow:                        0 1px 2px rgba(0,0,0,.6);

$carousel-control-color:                      #fff;
$carousel-control-width:                      15%;
$carousel-control-opacity:                    .5;
$carousel-control-font-size:                  20px;

$carousel-indicator-active-bg:                #fff;
$carousel-indicator-border-color:             #fff;

$carousel-caption-color:                      #fff;


// Close
// ------------------------
$close-color:                 #000;
$close-font-weight:           bold;
$close-text-shadow:           0 1px 0 #fff;


// Code
// ------------------------
$code-color:                  #c7254e;
$code-bg:                     #f9f2f4;

$pre-bg:                      $gray-lighter;
$pre-color:                   $gray-dark;
$pre-border-color:            #ccc;
$pre-scrollable-max-height:   340px;

// Type
// ------------------------
$text-muted:                  $gray-light;
$abbr-border-color:           $gray-light;
$headings-small-color:        $gray;
$blockquote-small-color:      $gray-light;
$blockquote-border-color:     $gray-light;
$page-header-border-color:    $gray;

// Miscellaneous
// -------------------------

// Hr border color
$hr-border:                   $gray;
$hr-border-light:             $gray-light;

// Horizontal forms & lists
$component-offset-horizontal: 180px;


// Container sizes
// --------------------------------------------------

// Small screen / tablet
$container-tablet:            ((720px + $grid-gutter-width));

// Medium screen / desktop
$container-desktop:           ((940px + $grid-gutter-width));

// Large screen / wide desktop
$container-lg-desktop:        ((1140px + $grid-gutter-width));
