.modal-dialog-drawer {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding: 0px;
  margin-bottom:10px;
  z-index: ($zindex-modal-background + 10);
}

.modal-dialog-drawer {
  .page-header-button {
    text-transform: Uppercase;
    padding-bottom: 30px;
    margin: 0px 0px 30px 0px;
    border-bottom: 1px solid $page-header-border-color;
    position: relative;

    h1 {
      margin-bottom:0px;
      margin-right:40px;
    }

    button.close-modal {
      position: absolute;
      top:10px;
      right:0px;
      @media (min-width: $screen-tablet) {
        top:0px;
        right:0px;
      }
    }


  }
}



// Actual modal
.modal-content {
  position: relative;
  background-color: $modal-content-bg;
  border: 1px solid $modal-content-fallback-border-color; //old browsers fallback (ie8 etc)
  border: 1px solid $modal-content-border-color;
  border-radius: 0px;
  @include box-shadow(0 3px 9px rgba(0,0,0,.5));
  background-clip: padding-box;
  // Remove focus outline from opened modal
  outline: none;
}

// Modal background
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: ($zindex-modal-background - 10);
  background-color: rgba(0, 0, 0, .5);
  // Fade for backdrop
  &.fade { @include opacity(0); }
  &.in { @include opacity(.3); }
}

// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
   -webkit-border-top-left-radius: $border-radius-base;
  -webkit-border-top-right-radius: $border-radius-base;
       -moz-border-radius-topleft: $border-radius-base;
      -moz-border-radius-topright: $border-radius-base;
           border-top-left-radius: $border-radius-base;
          border-top-right-radius: $border-radius-base;
  background-color: $white;
  padding: 40px 40px 0px 40px;
  border-bottom: 0px solid $modal-header-border-color;
  min-height: ($modal-title-padding + $modal-title-line-height);
}


// Title text within header
.modal-title {
  margin: 0;
  line-height: $modal-title-line-height;
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.modal-body {
  position: relative;
  padding: 0px 40px 0px 40px;
}

// Footer (for actions)
.modal-footer {
  margin-top: 0px;
  padding: 40px;
  text-align: right; // right align buttons
  border-top: 0px solid $modal-footer-border-color;


  .modal-footer-content {
    padding-top: 0px;
  }




  // Properly space out buttons
  .btn + .btn {
    margin-left: 0px;
    margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs
  }
  // but override that for button groups
  .btn-group .btn + .btn {
    margin-left: -1px;
  }
  // and override it for block buttons as well
  .btn-block + .btn-block {
    margin-left: 0;
  }
}







