/* Common (global) cross-brand styles*/
@use '../common/common';

/* Generic utilities*/
@use '../utils/generate_font_faces' as gff;
@use '../utils/root-font' as rf;

/* Brand utilities and variables*/
@use 'fonts' as f;
@use 'colors' as c;

/* Core styling*/
@include gff.generate_font_faces(f.$font-list, f.$base-font-url-path);

@include rf.root-font(
	f.$font-family-roboto,
	c.$grey-dark,
	f.$base-font-sm,
	600px,
	f.$base-font-med,
	1500px,
	f.$base-font-lg
);
