//
// ALERTS - CITATION Style
// --------------------------------------------------


.cit-alert {
  position: relative;
  padding: 13px 16px;
  margin-bottom: $alert-margin-bottom;
  border-radius: 8px;
  color: $cit-type-gray-darker;

}


// Headings for larger alerts
.cit-alert-heading {
  // Specified to prevent conflicts of changing $headings-color
  color: inherit;
}

// Provide class for links that match alerts
.cit-alert-link {
  font-weight: $alert-link-font-weight;
}


.alert-attention {
  background-color: rgba($cit-yellow, .15);
  .icon {
    display: flex;
    width:30px;
    height:30px;
    margin-right: 16px;
  }
}


.alert-danger {
  background-color: rgba($cit-red, .15);
  .icon {
    display: flex;
    width:30px;
    height:30px;
    margin-right: 16px;
  }
}

.alert-success {
  background-color: rgba($cit-green, .15);
  .icon {
    display: flex;
    span {
      font-size: 30px;
      margin-right: 16px; 
      color: $cit-green;
    }
  }

}


// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.cit-alert-dismissible {
  padding-right: $close-font-size + $alert-padding-x * 2;

  // Adjust close link position
  .close {
    position: absolute;
    top: 0;
    right: 0;
    padding: $alert-padding-y $alert-padding-x;
    color: inherit;
  }
}


