
//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

// Core styles
.btn {
  display: inline-block;
  padding: $padding-base-vertical;
  margin-bottom: 0;
  // font-size: $font-size-base;
  font-size: inherit;
  font-weight: $btn-font-weight;
  line-height: $line-height-base;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  white-space: none;
  width: 100%;
  text-decoration: none;
  user-select: none;

  &:hover,
  &:focus {
    color: $btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    @include box-shadow(inset 0 0px 0px rgba(0,0,0,0));
  }



  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
    pointer-events: none; // Future-proof disabling of clicks
    opacity: .5;
    @include box-shadow(inset 0 0px 0px rgba(0,0,0,0));
  }

}



// DBaker: Classes below enable buttons and links to behave correctly for small and large screens

.btn-right {
  width:100%;
  margin-bottom:10px;
    @media (min-width: $screen-tablet) {
      width: auto;
      float: right;
      margin-left:0px;
      margin-bottom: 0px;
    }
}

.btn-left {
  width:100%;
  margin-bottom:10px;
    @media (min-width: $screen-tablet) {
      width: auto;
      margin-right:10px;
      margin-bottom: 0px;
    }
}


.btn-center {
  width:100%;
    @media (min-width: $screen-tablet) {
      width: auto;
      margin-bottom: 0px;
    }
}


.btn-right + .btn-right {
  margin-right:10px;
}

.btn-right + .btn-right,
.btn-left + .btn-left {
    margin-bottom: 0px;
  // @media (max-width: $screen-tablet) {
 //      margin-bottom: 10px;
 //    }
}


.btn-right + .btn-right + .btn-right,
.btn-left + .btn-left + .btn-left{
    margin-bottom: 0px;
    margin-right:0px;
    margin-top: 10px;
    @media (min-width: $screen-tablet) {
      margin-top: 0px;
    }
}



.btn-left + .btn-right,
.btn-right + .btn-left {
  margin-bottom: 0px;
}



.btn-left + .btn-link.btn-right,
.btn-right + .btn-link.btn-left {
  margin-bottom: 0px;
  padding:0px;
    @media (min-width: $screen-tablet) {
      padding: 6px 0px 6px 0px;
    }
}


.btn-right + .btn-link-secondary.btn-left {
  margin-bottom: 0px;
  padding:0px;
    @media (min-width: $screen-tablet) {
      padding: 6px 0px 6px 0px;
    }
}

.btn-left + .btn-link-secondary.btn-right {
    margin-bottom: 0px;
    padding:0px;
    @media (min-width: $screen-tablet) {
      padding: 0px 0px 0px 0px;
      float:none;
      margin-left: 0px;
      margin-top:10px;
      // margin: 10px 0px 0px 0px;
    }
    @media (min-width: $screen-desktop) {
      padding: 6px 0px 6px 0px;
      float: right;
      margin-top:5px;
    }
}




.btn-left.stacked,
.btn-right.stacked{
   margin-right: 0px;
   margin-bottom: 10px;
   display: block;
}




.btn-left.stacked + .btn-link-secondary.btn-left.stacked {
  margin-right: 0px;
  margin-bottom: 0px;
  display: block;
}

.btn-left.stacked + .btn-link-secondary.btn-right.stacked {
  margin-right: 0px;
  margin-bottom: 0px;
  display: block;
  float: none;
}





ul + button.btn-right {
  margin-bottom: 0px;
  margin-top: 10px;
    @media (min-width: $screen-tablet) {
      float:none;
      margin-left: 0px;
      margin-top:10px;
      // margin: 10px 0px 0px 0px;
    }
    @media (min-width: $screen-desktop) {
      float: right;
      margin-top:0px;
    }
}

ul + a.btn-right {
  margin-bottom: 0px;
  margin-top: 10px;
    @media (min-width: $screen-tablet) {
      float:none;
      margin-left: 0px;
      margin-top:10px;
      // margin: 10px 0px 0px 0px;
    }
    @media (min-width: $screen-desktop) {
      float: right;
      margin-top:0px;
    }
}




// Alternate buttons
// --------------------------------------------------

.btn-default {
  @include button-variant($btn-default-color, $btn-default-bg, $btn-default-border);
}


.btn-primary {
  @include button-variant-primary($btn-primary-color, $btn-primary-bg, $btn-primary-border);
}

.btn-secondary {
  @include button-variant-secondary($btn-secondary-color, $btn-secondary-bg, $btn-secondary-border);
}

.btn-blue {
  @include button-variant-blue($btn-onstar-color, $btn-onstar-bg, $btn-onstar-border);
}


// Warning appears as orange
.btn-warning {
  @include button-variant($btn-warning-color, $btn-warning-bg, $btn-warning-border);
}
// Danger and error appear as red
.btn-danger {
  @include button-variant($btn-danger-color, $btn-danger-bg, $btn-danger-border);
}
// Success appears as green
.btn-success {
  @include button-variant($btn-success-color, $btn-success-bg, $btn-success-border);
}
// Info appears as blue-green
.btn-info {
  @include button-variant($btn-info-color, $btn-info-bg, $btn-info-border);
}

// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: $link-color;
  white-space: normal;
  font-weight: bold;
  cursor: pointer;
  padding: 0px 0px 0px 0px; // DB added
  text-align: left; // DB added 6/27/14
  display: inline;
  text-decoration: none;
  // border-bottom: 1px solid $link-color;
  // padding-bottom: 2px;

  &,
  &:active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    @include box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    // border-color: transparent;
  }
  &:hover,
  &:focus {
    color: $blue-light;
    text-decoration: none;
    // border-bottom: 1px solid $blue-light;
    background-color: transparent;

  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: $btn-link-disabled-color;
      text-decoration: none;
    }
  }
}

.btn-link-secondary {
  color: $gray-dark;
  font-weight: bold;
  white-space: normal;
  cursor: pointer;
  border-radius: 0;
  text-decoration:none;
  outline: 0;
  padding: 0px; // DB added
  text-align: left; // DB added 6/27/14


  &,
  &:active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    @include box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: darken($gray-dark, 8%);
    text-decoration: none;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: $btn-link-disabled-color;
      text-decoration: none;
    }
  }
}



// Button Sizes
// --------------------------------------------------




.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  @include button-size($padding-large-vertical, $padding-large-horizontal, $font-size-large, $line-height-large, $border-radius-large);
}
.btn-sm,
.btn-xs {
  // line-height: ensure proper height of button next to small input
  @include button-size($padding-small-vertical, $padding-small-horizontal, $font-size-small, $line-height-small, $border-radius-small);
}
.btn-xs {
  padding: 1px 5px;
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}

.stacked-buttons{
  a.btn {
    display: block;
    margin-bottom: 10px;
    @media (min-width: $screen-tablet) {
      max-width: 200px;
    }
    @media (min-width: $screen-desktop) {
      max-width: 303px;
    }
    &:last-child {
    margin-bottom: 0px;
    }
  }
}


.card.card-default{
  .stacked-buttons{
    a.btn {
      display: block;
      margin-bottom: 10px;
      @media (min-width: $screen-tablet) {
        max-width: 100%;
      }
      @media (min-width: $screen-desktop) {
        max-width: 100%;
      } 
      &:last-child {
      margin-bottom: 0px;
      }
    }
  }
}

// MAinly used fo Buttons within container 6-24-16 DB
.inline-buttons {
  overflow: hidden;
  @media (min-width: $screen-tablet) {
    margin-bottom: 10px;
  }
  .inline-button-container {
    margin-bottom: 10px;
    @media (min-width: $screen-tablet) {
      float: left;
      width:50%;

      padding-right: 10px;
      margin-bottom: 0px;
    }
    &:last-child {
      padding-right: 0px;
      margin-bottom: 0px;
    }
  }
}

// MAinly used for link left and two inline Buttons on the right 6-24-16 DB
.inline-buttons-right {
    float: none;
  @media (min-width: $screen-tablet) {
    margin-top: 0px;
    float: right;
  }

  a.btn {
    margin-bottom: 10px;
    width:100%;
    margin-top: 10px;
    @media (min-width: $screen-tablet) {
      width: auto;
      margin-bottom: 0px;
      margin-right: 10px;
      margin-top: 0px;
    }
    &:last-child {
      margin-right: 0px;
      margin-top: 0px;
      margin-bottom: 0px;
      @media (min-width: $screen-tablet) {
        width: auto;
        margin-bottom: 0px;
        margin-right: 0px;
        margin-top: 0px;
      }
    }
  }
}






