//
// Progress bars
// --------------------------------------------------


// Bar animations
// -------------------------

// Webkit
@-webkit-keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}

// Firefox
@-moz-keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}

// Opera
@-o-keyframes progress-bar-stripes {
  from  { background-position: 0 0; }
  to    { background-position: 40px 0; }
}

// Spec and IE10+
@keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}


// DB: Containes the Progress bar in order to manipulate its layout
// -------------------------

.progress-container {
  text-align: center;
  margin-bottom: 20px;
  p {
    margin-bottom: 0px;
  }

}



// Bar itself
// -------------------------

// Outer container
.progress {
  overflow: none;
  height: 4px;
  margin-bottom: 5px;
  background-color: transparent;
  border-radius: none;
  box-shadow: none;
}


.progressBody {
  display: block;
  margin-bottom:20px;
}



// Bar of progress
.progress-bar {
  width: 0%;
  height: 100%;
  font-size: $font-size-base;
  color: $progress-bar-color;
  text-align: center;
  background-color: transparent;
  float: none;
  box-shadow: none;
  @include transition(width .6s ease);
}







