/* STRENGTH METER - password strength meter
 * ---------------------------------- */

.strength-meter {
  overflow:visible;
  margin-top:0px;
  margin-bottom:10px;
  @media (min-width: $screen-tablet) {
    margin-top:10px;
  }
}

.strength-meter-fill{
  margin-right:8px;
  height:4px;
  float: left;
}

.strength-meter-text{
  font-size: $font-size-base;
  line-height:5px;
  margin-left:10px;
  overflow:visible;
  margin-top:0px;
  @media (min-width: $screen-tablet) {
    margin-top:15px;
  }
}




// Variations
// -------------------------

.strength-meter-weak {
  @include strength-meter-variant($progress-bar-danger-bg);
  width: 31%;
}

.progress-bar-ok {
  @include strength-meter-variant($progress-bar-warning-bg);
  width: 62%;
}

.strength-meter-strong {
  @include strength-meter-variant($progress-bar-success-bg);
  width: 86%;
}








// Timeline
// -------------------------

.timeline-container {
  overflow: auto;
  padding: 36px 20px 0px 20px;
}

.timeline {
  margin: 0px 0px 55px 0px;
  position: relative;
  width: 5002px;
  padding:10px 34px;
}

.timeline-marker{
  top:-35px;
  margin-left: 8px;
  padding: 5px;
  background-color: $gray-light;
  border: 1px solid $gray;
  display: inline-block;
  // Makes IE perform inline-block
  zoom: 1;
  *display: inline;
  //-----------------------------
  position: absolute;
  z-index: 100;
  &:after {
    position: absolute;
    top: 44px;
    left: 40%;
    border: solid transparent;
    content: " ";
    height: 0px;
    width: 0px;
    border-top: 10px solid $gray-light;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    border-color: rgba(240,240,240,1) rgba(0,0,0,0) rgba(0,0,0,0)  rgba(0,0,0,0);
  }
  &:before {
    position: absolute;
    top: 45px;
    left: 39%;
    border: solid transparent;
    content: " ";
    height: 0px;
    width: 0px;
    border-top: 11px solid $gray;
    border-right: 11px solid transparent;
    border-left: 11px solid transparent;
    border-color: rgba(211,211,211,1) rgba(0,0,0,0) rgba(0,0,0,0)  rgba(0,0,0,0);
  }
}

.timeline-current-mileage {
  text-align: center;
  margin:0px;
  p {
    margin:0px;
  }
}

// Timeline Progress Outer Stroke
.progress-timeline-border {
  border: 1px solid $gray;
  padding:10px 20px; 
  background-color: $white;
}

// Timeline Progress Bar Under
.progress-timeline {
  height: 10px;
  margin-bottom: $line-height-computed;
  background-color: $gray-light;
  position: relative;
}

// Timeline Progress Bar Over
.progress-bar-timeline {
  float: left;
  height: 100%;
}

