// Allows horizontal scroll bar to have 20px on right side
.extension-scroll-hack {
  position: absolute;
  left: 100%;
  width: 20px;
  height: 1px;
  margin: 0;
  background: transparent;
}


// TIMELINE TABS/EVENTS
ul.progress-timeline-tabs {
  margin:0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  position: relative;
  top: -5px;
  left: -10px;
  li.progress-timeline-tab-dot{
    position: absolute;
    content: none;
    img {
      float: left;
    }
  } 
}


.tab-event-marker{
  top:25px;
  left:-37px;
  margin: 0px;
  padding: 5px;
  background-color: transparent;
  border: 1px solid transparent;
  display: inline-block;
  // Makes IE perform inline-block
  zoom: 1;
  *display: inline; 
  //-----------------------------
  text-align: center;
  position: absolute;
  z-index: 100;
  .tab-event {
    width:81px;
    text-align: center;
    margin:0px;
    p {
      color:$gray;
      margin:0px;
    }
  }
}


.active .tab-event-marker {
  background-color: $white;
  border: 1px solid $gray;
  border-color: rgba(211,211,211,1);
  &:after {
    float: left;
    padding:0px;
    position: absolute;
    top: -13px;
    left: 40%;
    border: solid transparent;
    content: " ";
    height: 13px;
    width: 0px;
    border-bottom: 10px solid $white;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    border-color: rgba(0,0,0,0) rgba(0,0,0,0) rgba(255,255,255,1) rgba(0,0,0,0);
  }
  &:before {
    padding:0px;
    position: absolute;
    top: -15px;
    left: 39%;
    border: solid transparent;
    content: " ";
    height: 15px;
    width: 0px;
    border-bottom: 11px solid $gray;
    border-right: 11px solid transparent;
    border-left: 11px solid transparent; 
    border-color: rgba(0,0,0,0) rgba(0,0,0,0) rgba(211,211,211,1) rgba(0,0,0,0);
  }
  .tab-event {
    p {
      color: inherit;
      margin:0px;
    }
  }
}

