@use '../../common/common' as com;
@use '../../utils/breakpoints' as bp;

@use 'button' as b;
@use '../../chevy/colors' as c;
@use '../../chevy/fonts' as f;
@use '../../chevy/icons' as i;

%button-icon-base {
	svg,
	img {
		height: i.$icon-small;
		vertical-align: middle;
		width: i.$icon-small;

	  	@media print,only screen and (min-width:800px) {
			height: i.$icon-large;
			width: i.$icon-large;
	  	}
	}

	&.icon-stacked {
		svg,
		img {
			display: block;
			margin: auto auto calc(0.23097 * #{com.$gmux-base-font});

			@include bp.medium-and-above {
				margin: auto auto calc(0.27173 * #{com.$gmux-base-font});
			}
		
			@include bp.large-and-above {
				margin: auto auto calc(0.31249 * #{com.$gmux-base-font});
			}
		}
	}

	&.icon-left {
		svg,
		img {
			left: 1px;
			margin-right: 5px;
			position: relative;
			top: -2px;

			@media print,only screen and (min-width:800px) {
				margin-right: 7px;
			}
		}
	}

	&.icon-right {
		svg,
		img {
			margin-left: 5px;
			position: relative;
		  	right: 0;
			top: -2px;

			@media print,only screen and (min-width:800px) {
				margin-left: 7px;
			}
		}
	}
}

