%vpicker-gb-secondary-button {
@include b.primary-button(
		transparent,
		2px solid c.$grey-mid,
		c.$grey-dark,
		f.$font-family-roboto,
		14px,
		f.$font-weight-bold,
		normal,
		23px,
		9px 20px 8px,
		background .25s cubic-bezier(0, 1, .5, 1),
		c.$grey-mid-dark,
		2px solid c.$grey-mid-dark,
		c.$white
	);
	-o-object-fit: contain;
	   object-fit: contain;

	&.gb-small-button {
		line-height: 2;
		padding: 0;
		position: relative;

		&::after {
			content: '';
			height: 170%;
			left: 0;
			position: absolute;
			top: -25%;
			visibility: visible;
			width: 100%;
		}
	}

	&.disabled {
		opacity: .5;

		&:hover {
			background-color: transparent;
			border: 2px solid c.$grey-mid;
			box-shadow: none;
			color: c.$grey-dark;

			&:focus,
			&:active {
				background-color: transparent;
				border: 2px solid c.$grey-mid;
				box-shadow: none;
				color: c.$grey-dark;
			}
		}

		&:active {
			background-color: transparent;
			border: 2px solid c.$grey-mid;
			box-shadow: none;
			color: c.$grey-dark;

			&:focus {
				background-color: transparent;
				border: 2px solid c.$grey-mid;
				color: c.$grey-dark;
			}
		}
	}

	&:hover {
		&:active {
			box-shadow: none;
		}

		&:focus {
			background-color: c.$grey-mid-dark;
			border: 2px solid c.$grey-mid-dark;
			box-shadow: 0 0 3px 3px rgba(c.$grey, .5);
			color: c.$white;
		}
	}

	&:focus {
		background-color: transparent;
		border: 2px solid c.$grey-mid;
		box-shadow: 0 0 3px 3px rgba(c.$grey, .5);
		color: c.$grey-dark;

		&:active {
			background-color: c.$grey-mid-dark;
			border: 2px solid c.$grey-mid-dark;
			color: c.$white;
		}

		&.disabled {
			box-shadow: none;
		}
	}

	&.invert {
		@include b.button-invert(
			c.$grey-very-dark,
			2px solid c.$grey-mid,
			c.$white,
			c.$grey-mid-dark,
			c.$white
		);

		border: 2px solid c.$grey-mid-dark;
	}

	/* For Inline and Stacked Icon Styling*/
	@extend %button-icon-base;

	@media print,only screen and (min-width:800px) {
	  	font-size: 18px;
	  	line-height: 32px;
		padding: 10px 20px;
	}
}

